.xx {	
	font-family: 'Dosis', Calibri, sans-serif;
	color: #fff;
	font-weight: 50;
	letter-spacing: 5px;
  text-shadow:  0px 0px 10px #9D00FF,
                0px 0px 20px #9D00FF,
                0px 0px 30px #9D00FF,
                0px 0px 40px #9D00FF,
                0px 0px 50px #9D00FF,
                0px 0px 60px #9D00FF;
}

span.flick-4 {
  animation: flick-1 6s infinite;
}

span.flick-3 {
  animation: flick-2 3s infinite;
}

span.flick-2 {
  animation: flick-3 5s infinite;
}


span.flick-1 {
  animation: flick-4 1s infinite;
}

@keyframes flick-4 {
  0% { opacity: 1; }
  52% { opacity: 1; }
  55% { opacity: 0; }
  56% { opacity: 1; }
  100% { opacity: 1; }
}
            
@keyframes flick-2 {
  0% { opacity: 1; }
  62% { opacity: 1; }
  65% { opacity: 0; }
  66% { opacity: 1; }
  69% { opacity: 0; }
  70% { opacity: 1; }
  73% { opacity: 0; }
  74% { opacity: 1; }
  100% { opacity: 1; }
}
            
@keyframes flick-3 {
  0% { opacity: 1; }
  82% { opacity: 1; }
  84% { opacity: 0; }
  85% { opacity: 1; }
  87% { opacity: 0; }
  88% { opacity: 1; }
  90% { opacity: 0; }
  91% { opacity: 1; }
  100% { opacity: 1; }
}

@keyframes flick-1 {
  0% { opacity: 1; }
  25% { opacity: 0; }
  28% { opacity: 1; }
  45% { opacity: 0; }
  49% { opacity: 1; }
  65% { opacity: 0; }
  70% { opacity: 1; }
  73% { opacity: 0; }
  82% { opacity: 1; }
  84% { opacity: 0; }
  85% { opacity: 1; }
  87% { opacity: 0; }
  88% { opacity: 1; }
  90% { opacity: 0; }
  91% { opacity: 1; }
  100% { opacity: 1; }
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: grey;
  opacity: 1; /* Firefox */
}